.location {
  position: relative;
  background-image: url("/img/location-bg.png");
  background-position: right;
  background-size: contain;
  background-repeat: no-repeat;

  &__bg {
    background: linear-gradient(180deg, rgba(24, 24, 24, 0.9) 3.47%, rgba(24, 24, 24, 0.72) 100%);
  }

  &__container {
    padding-bottom: 65px;
  }

  &__form {}

  &__box {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 275px;
    margin: 0 auto;
    width: 925px;

    @include tablet {
      width: 100%;
    }

    @include small-tablet {
      padding-top: 370px;
    }

    @include mobile {
      padding-top: 400px;
    }
  }

  &__address {
    margin-bottom: 25px;
    max-width: 590px;
    font-family: 'Yeseva One';
    font-weight: 400;
    font-style: normal;
    font-size: 20px;
    line-height: 28px;
    text-align: center;
    text-transform: uppercase;
    color: #E3B873;

    @include mobile {
      font-size: 16px;
    }
  }

  &__social {
    margin-bottom: 40px;
  }

  &__map {
    height: 465px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @include tablet {
      width: 100%;
    }

    @include mobile {
      height: 250px;
    }
  }

  .form-contacts {
    position: absolute;
    left: 50%;
    top: -228px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 35px 180px;
    width: 880px;
    background: #131313;
    box-shadow: 0px 30px 30px rgba(0, 0, 0, 0.05);
    transform: translateX(-50%);

    @include tablet {
      padding: 35px 50px;
      width: 90%;
    }

    @include mobile {
      padding: 30px;
      width: 100%;
    }
  }

  .form-contacts__top {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 48px;
    width: 100%;
    height: 100%;
  }

  .form-contacts__subtitle {
    margin: 0;
    margin-bottom: 45px;
    font-size: 20px;
    text-align: center;
    color: #fff;
  }

  .form-field__caption {
    color: rgba(255, 255, 255, 0.5);
  }

  .form-field__input {
    width: 100%;
    background-color: #131313;
  }

  .form-field__input::placeholder {
    color: #fff;
  }

  .form-field {
    margin-bottom: 45px;
    width: 231px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.15);

    @include small-tablet {
      width: 100%;
    }
  }

  .form-field:nth-child(2) {
    margin-left: 58px;

    @include small-tablet {
      margin-left: 0;
    }
  }

  .form-field:nth-child(3) {
    margin-bottom: 0;
    width: 100%;
  }

  .custom-checkbox__text {
    color: #fff;
  }

  .form-contacts__bottom {
    display: flex;
    flex-direction: column;
  }

  .form-contacts__btn {
    margin-top: 50px;
    padding: 20px 75px;
    box-shadow: 0px 5px 15px rgba(227, 184, 115, 0.15);
  }
}

.social {
  display: flex;
  align-items: center;

  &__item {
    &:not(:last-child) {
      margin-right: 15px;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    width: 28px;
    height: 28px;
    overflow: hidden;
    background-color: #E3B873;
    transition: background-color 0.3s ease-in;

    svg {
      width: 14px;
      height: 11px;
      fill: #fff;
    }

    &:hover {
      background-color: #f2a830;
    }
  }
}
