.advantage {
  position: relative;
  padding-top: 70px;
  padding-bottom: 100px;
  background-image: url("/img/advantage-bg.png");
  background-position: left;
  background-size: contain;
  background-repeat: no-repeat;
  background-color: #fff;
  overflow: hidden;

  &__container {
    display: flex;
    justify-content: space-between;

    @include small-tablet {
      flex-direction: column;
    }
  }

  &__list {
    padding-right: 30px;
    padding-bottom: 45px;
    padding-left: 195px;

    @include tablet {
      padding-left: 0;
    }

    @include small-tablet {
      padding-right: 0;
      width: 100%;
    }
  }

  &__item {
    display: flex;
    align-items: center;

    &:not(:last-child) {
      margin-bottom: 90px;

      @include tablet {
        margin-bottom: 70px;
      }

      @include small-tablet {
        margin-bottom: 40px;
      }

      @include mobile {
        margin-bottom: 20px;
      }
    }
  }

  &__nume {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    margin-right: 15px;
    border: 8px solid #FFFFFF;
    box-shadow: 0px 5px 15px rgba(227, 184, 115, 0.15);
    border-radius: 100%;
    width: 50px;
    height: 50px;
    font-family: 'Yeseva One';
    font-weight: 400;
    font-size: 18px;
    color: #FFFFFF;
    background-color: #E3B873;
  }

  &__descr {
    margin: 0;
    max-width: 315px;
    font-size: 14px;
    line-height: 19px;
    color: #363636;
  }

  &__form {}

  &__share {}

  .form-contacts {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px 50px;
    width: 506px;
    height: 520px;
    background: #131313;
    box-shadow: 0px 30px 30px rgba(0, 0, 0, 0.05);

    @include mobile {
      padding: 30px;
      height: auto;
      width: 100%;
    }
  }

  .form-contacts__top {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    height: 100%;
  }

  .form-contacts__descr {
    margin: 0;
    margin-bottom: 30px;
    max-width: 300px;
    font-size: 18px;
    line-height: 26px;
    text-align: center;
    color: #FFFFFF;
  }

  .form-field__caption {
    color: rgba(255, 255, 255, 0.5);
  }

  .form-field__input {
    width: 100%;
    background-color: #131313;
  }

  .form-field__input::placeholder {
    color: #fff;
  }

  .form-field {
    margin-bottom: 45px;
    width: 100%;
    border-bottom: 1px solid rgba(255, 255, 255, 0.15);
  }

  .form-field:nth-child(3) {
    margin-bottom: 20px;
  }

  .custom-checkbox__text {
    color: #fff;
  }

  .form-contacts__bottom {
    justify-content: flex-end;
  }

  .form-contacts__btn {
    margin-top: auto;
    padding: 18px 22px;
    box-shadow: 0px 5px 15px rgba(227, 184, 115, 0.15);
  }

  @include tablet {
    padding-bottom: 140px;
  }

  @include small-tablet {
    padding-top: 0;
  }

  @include mobile {
    padding-bottom: 0;
  }
}

.share {
  position: absolute;
  right: 180px;
  bottom: 20px;
  display: flex;
  align-items: center;

  &__image {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    margin-right: 25px;
    width: 72px;
    height: 72px;
    background-color: #E3B873;
  }

  &__descr {
    max-width: 250px;
    font-size: 18px;
    line-height: 26px;
    color: #E3B873;
    text-shadow: 0px 3px 10px rgba(227, 184, 115, 0.15)
  }

  @include mobile {
    display: none;
  }
}
