.footer {
  display: flex;
  align-items: center;
  padding: 20px 0;
  background-color: #131313;

  &__container {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
}

.footer-contacts {
  display: flex;
  align-items: center;

  &__link {
    display: inline-block;
    margin-right: 45px;
    font-size: 11px;
    line-height: 15px;
    text-decoration-line: underline;
    color: #E3B873;
  }

  &__btn {}
}
