.header {
  display: flex;
  align-items: center;
  padding: 25px 0;
  background-color: #131313;

  &__container {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
}

.logo {
  display: flex;
  align-items: center;
  width: 180px;

  img {
    display: block;
    width: 100%;
    object-fit: cover;
  }

  @include mobile {
    width: 160px;
  }
}

.nav {
  display: flex;
  align-items: center;

  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 47px;

      @include desktop {
        margin-right: 30px;
      }
    }
  }

  &__link {
    font-size: 11px;
    line-height: 15px;
    color: #FFFFFF;
    transition: color 0.2s ease-in-out;

    &--current {
      color: #E3B873;
    }

    &:hover {
      color: #E3B873;
    }
  }

  @include tablet {
    display: none;
  }
}

.contacts {
  display: flex;
  align-items: center;

  &__link {
    display: inline-block;
    margin-right: 45px;
    font-size: 11px;
    line-height: 15px;
    text-decoration-line: underline;
    color: #E3B873;
    transition: color 0.2s ease-in-out;

    &:hover {
      color: #fff;
    }

    @include desktop {
      margin-right: 30px;
    }

    @include tablet {
      margin-right: 80px;
    }

    @include mobile {
      margin-right: 0;
      color: #fff;
      text-decoration: none;
    }
  }

  &__btn {
    @include mobile {
      display: none;
    }
  }
}
