.page-hero {
  min-height: 100vh;
  background-image: url('/img/image-bg.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  &__container {
    padding-top: 195px;
    padding-bottom: 208px;

    @include mobile {
      padding-top: 80px;
      padding-bottom: 100px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    max-width: 515px;
    font-family: 'Yeseva One';
    font-weight: 400;
    font-size: 40px;
    line-height: 53px;
    text-transform: uppercase;
    color: #FFFFFF;

    span {
      color: #E3B873;
    }

    @include small-tablet {
      max-width: 330px;
    }

    @include mobile {
      font-size: 22px;
      line-height: 33px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 40px;
    max-width: 505px;
    font-size: 22px;
    line-height: 36px;
    color: #FFFFFF;

    @include mobile {
      font-size: 14px;
      line-height: 20px;
    }
  }

  &__buttons {
    display: flex;
    align-items: center;

    @include mobile {
      flex-wrap: wrap;
    }
  }

  &__btn {
    padding: 20px 40px;

    &:not(:last-child) {
      margin-right: 20px;

      @include mobile {
        margin-right: 0;
        margin-bottom: 30px;
      }
    }

    @include mobile {
      width: 100%;
    }
  }
}
