.control {
  padding-bottom: 70px;
  background-color: #fff;

  &__container {}

  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 817px;
    margin: 0 auto;
  }

  &__title {
    margin-top: -310px;
    font-size: 20px;
    line-height: 28px;
    text-align: center;
    color: #fff;

    @include mobile {
      margin-top: 0;
    }
  }

  &__caption {
    margin-bottom: 30px;
  }

  &__text {
    position: relative;
    margin: 0;
    margin-bottom: 90px;
    padding-top: 35px;
    max-width: 680px;
    font-size: 18px;
    line-height: 37px;
    text-align: center;
    color: #363636;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      width: 55px;
      height: 2px;
      background-color: #E3B873;
      transform: translateX(-50%);
    }

    @include mobile {
      margin-bottom: 60px;
      font-size: 12px;
      line-height: 169.8%;
    }
  }

  &__form {}
}

.caption {
  position: relative;
  padding: 0 43px;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    border-radius: 30px;
    width: 32px;
    height: 2px;
    background-color: #E3B873;
  }

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    border-radius: 30px;
    width: 32px;
    height: 2px;
    background-color: #E3B873;
  }

  &__image {
    width: 33px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.video-block {
  position: relative;
  z-index: 10;
  margin-bottom: 60px;
  width: 100%;
  height: 435px;
  overflow: hidden;

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    z-index: 3;
    background-color: rgba(0, 0, 0, 0.8);
  }

  &__play {
    position: absolute;
    top: 50%;
    left: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 50;
    border-radius: 100%;
    width: 85px;
    height: 85px;
    background-color: #E3B873;
    box-shadow: 0px 5px 30px rgba(227, 184, 115, 0.2);
    transform: translate(-50%, -50%);
    transition: background-color 0.2s ease-in;

    svg {
      width: 28px;
      height: 28px;
      fill: #fff;

      @include mobile {
        width: 15px;
        height: 15px;
      }
    }

    &:hover {
      background-color: #f6a41f;
    }

    @include mobile {
      width: 37px;
      height: 37px;
    }
  }

  &__content {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  @include small-tablet {
    height: 300px;
  }

  @include mobile {
    height: 250px;
  }
}

.form-contacts {
  width: 95%;

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 40px;

    @include small-tablet {
      flex-direction: column;
    }
  }

  &__field {
    @include small-tablet {
      width: 70%;
      margin-bottom: 30px;
    }

    @include mobile {
      width: 100%;
    }
  }

  &__btn {
    padding: 17px 75px;
    font-size: 13px;

    @include mobile {
      width: 100%;
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
  }
}

.form-field {
  padding-bottom: 5px;
  border-bottom: 1px solid rgba(54, 54, 54, 0.3);

  &__caption {
    display: block;
    font-size: 13px;
    line-height: 21px;
    color: rgba(54, 54, 54, 0.5);
  }

  &__input {
    width: 230px;
    font-size: 14px;
    line-height: 23px;

    &::placeholder {
      color: #363636;
    }
  }
}

.custom-checkbox {
  display: flex;
  align-items: center;
  cursor: pointer;

  &__input {
    position: absolute;
    opacity: 0;
    z-index: 10;
  }

  &__text {
    position: relative;
    display: inline-block;
    padding-left: 28px;
    font-size: 14px;
    line-height: 163.68%;
    color: #181818;

    &::before {
      content: "";
      position: absolute;
      left: -1px;
      top: 1px;
      display: inline-block;
      border-radius: 1px;
      border: 1px solid #E3B873;
      width: 18px;
      height: 18px;
      transition: background-color 0.3s ease;
    }

    &::after {
      content: "";
      position: absolute;
      left: -1px;
      top: 1px;
      display: inline-block;
      border-radius: 1px;
      width: 18px;
      height: 18px;
      background-image: url("/img/check.png");
      background-position: center;
      background-size: 10px 8px;
      background-repeat: no-repeat;
      opacity: 0;
      transition: opacity 0.3s ease;
    }
  }
}

.custom-checkbox__input:checked+.custom-checkbox__text::before {
  background-color: #E3B873;
}

.custom-checkbox__input:checked+.custom-checkbox__text::after {
  opacity: 1;
}
