.repair {
  padding: 70px 0;

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    color: #181818;
  }

  &__caption {
    margin-bottom: 30px;
  }

  &__columns {
    display: flex;
    justify-content: space-between;
    gap: 30px;
    width: 100%;

    @include tablet {
      flex-direction: column;
      gap: 90px;
      padding-bottom: 60px;
    }

    @include mobile {
      padding-bottom: 0;
    }
  }

  @include tablet {
    padding-top: 0;
  }
}

.repair-column {
  flex: 1 1 33.333%;

  &__image {
    img {
      display: block;
      width: 100%;
      object-fit: cover;
    }
  }

  &__inner {
    position: relative;
    z-index: 5;
    margin: 0 auto;
    margin-top: -130px;
    padding: 25px 25px 50px 35px;
    width: 330px;
    background-color: #fff;
    box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.03);

    @include desktop {
      margin-left: 0;
      width: 90%;
    }

    @include tablet {
      width: 60%;
    }

    @include mobile {
      width: 80%;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 13px;
    font-family: 'Yeseva One';
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    color: #363636;
  }

  &__list {}

  &__item {
    position: relative;
    display: flex;
    align-items: center;

    &:not(:last-child) {
      margin-bottom: 12px;
    }

    &::before {
      content: "";
      position: absolute;
      top: 3px;
      left: 0;
      border-radius: 100%;
      width: 5px;
      height: 5px;
      background-color: #959595;
    }
  }

  &__descr {
    margin: 0;
    padding-left: 12px;
    font-size: 13px;
    color: #959595;
  }

  &__price {
    position: absolute;
    bottom: -28px;
    left: 0;
    padding: 17px 22px;
    max-width: 228px;
    background-color: #E3B873;
    box-shadow: 5px 5px 10px rgba(227, 184, 115, 0.3);
  }

  &__value {
    margin: 0;
    font-family: 'Yeseva One';
    font-weight: 400;
    font-size: 18px;
    text-transform: uppercase;
    color: #fff;
  }
}
