.desing {
  margin-top: 240px;
  margin-bottom: 110px;
  background-color: #fff;
  background-image: url("/img/desing-bg.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;


  &__bg {
    background: linear-gradient(109.8deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0.9) 100%);
  }

  &__container {
    position: relative;
    display: flex;
  }

  &__left {
    flex: 0 1 375px;
    margin: 138px 78px 120px 100px;

    @include desktop {
      margin-left: 30px;
    }

    @include mobile {
      margin: 0;
      padding: 30px;
    }
  }

  &__slogan {
    margin: 0;
    margin-bottom: 45px;
    font-size: 27px;

    @include mobile {
      font-size: 22px;
      line-height: 138.6%;
    }
  }

  &__list {}

  &__item {
    &:not(:last-child) {
      margin-bottom: 25px;
    }
  }

  &__link {
    font-size: 20px;
    color: #fff;

    &--current {
      border-bottom: 1px solid currentColor;
      color: #E3B873;
    }

    &:hover {
      border-bottom: 1px solid currentColor;
      color: #E3B873;
    }

    @include mobile {
      font-size: 18px;
    }
  }

  &__grid {
    position: absolute;
    top: -38px;
    right: 100px;
    display: grid;
    grid-template-columns: repeat(2, 354px);
    grid-gap: 40px;

    @include desktop {
      right: 0;
      grid-template-columns: repeat(2, 45%);
      width: 670px;
    }

    @include tablet {
      right: 15px;
      gap: 20px;
      // width: 400px;
      width: 53%;
    }

    @include small-tablet {
      display: none;
    }
  }

  @include small-tablet {
    margin-top: 70px;
    margin-bottom: 0;
  }
}

.grid-image {
  img {
    display: block;
    width: 100%;
    height: 100%;
  }
}

.grid-image1 {
  height: 558px;
}

.grid-image2 {
  margin-top: -70px;
  height: 275px;
}

.grid-image3 {
  height: 350px;
}

.grid-image4 {
  margin-top: -356px;
  height: 625px;
}
