.projects {
  padding-top: 105px;
  padding-bottom: 415px;
  background-color: #131313;

  &__container {
    position: relative;
  }

  &__nav {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-right: 55px;

    @include mobile {
      display: none;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 60px;

      @include mobile {
        margin-right: 30px;
      }
    }
  }

  &__btn {
    position: relative;
    padding-bottom: 6px;
    font-weight: 400;
    font-family: 'Yeseva One';
    font-size: 14px;
    line-height: 19px;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.3);


    &--current {
      border-bottom: 1px solid currentColor;
      color: #E3B873;
    }

    &:hover {
      border-bottom: 1px solid currentColor;
      color: #E3B873;
    }
  }

  &__slider {}

  &__content {
    margin-right: 50px;
    padding-top: 45px;
    padding-bottom: 40px;

    @include tablet {
      margin-right: 0;
    }

    @include mobile {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-top: 0;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 35px;
    width: 250px;
    letter-spacing: -0.07em;
    font-size: 27px;
    line-height: 37px;

    @include mobile {
      text-align: center;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 40px;
    line-height: 27px;
    color: #FFFFFF;

    @include tablet {
      width: 60%;
    }

    @include mobile {
      width: 100%;
      text-align: center;
      font-size: 14px;
      line-height: 135.84%;
    }
  }

  &__info {}

  &__image {
    padding-top: 45px;
    flex-shrink: 0;
    width: 680px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @include tablet {
      width: 100%;
    }

    @include mobile {
      padding-top: 0;
    }
  }

  &__btns {
    position: absolute;
    left: 15px;
    bottom: 0;
    display: flex;
    align-items: center;
    width: 50%;

    @include tablet {
      bottom: -50px;
    }

    @include mobile {
      display: none;
    }
  }

  @include mobile {
    padding-top: 40px;
    padding-bottom: 70px;
  }
}

.swiper-slide {
  display: flex;

  @include tablet {
    flex-wrap: wrap;
  }
}

.info {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;

  &__item {
    grid-column: 1 span;
  }

  &__title {
    margin: 0;
    margin-bottom: 10px;
    font-size: 18px;
    line-height: 25px;

    @include mobile {
      font-size: 14px;
      line-height: 138.6%;
    }
  }

  &__value {
    font-size: 13px;
    line-height: 21px;
    color: #FFFFFF;

    @include mobile {
      font-size: 11px;
      line-height: 135.84%;
    }
  }

  @include mobile {
    gap: 50px;
  }
}

.project-prev-btn,
.project-next-btn {
  margin-right: 30px;
  width: 41px;
  height: 14px;

  svg {
    width: 100%;
    height: 100%;
    fill: #fff;

    transition: fill 0.2s ease-in;

    &:hover {
      fill: #E3B873;
    }
  }

}

.project-next-btn {
  margin-right: 0;
  margin-left: 30px;
}

.project-pagination {
  margin-right: 18px;
  border-radius: 100%;
  width: 10px;
  height: 10px;
  background-color: rgba(255, 255, 255, 0.3);
}

.project-pagination:nth-child(4) {
  margin-right: 0;
}

.project-pagination--active {
  background-color: #fff;
}
