.clients {
  padding-bottom: 380px;
  background-image: url("/img/clients-bg.png");
  background-position: bottom;
  background-size: contain;
  background-color: #fff;

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &__title {
    color: #181818;
  }

  &__caption {
    margin-bottom: 25px;
  }

  &__descr {
    margin: 0;
    max-width: 420px;
    font-size: 18px;
    line-height: 29px;
    text-align: center;
    color: #363636;
  }

  &__card {
    margin-top: 40px;
  }

  @include tablet {
    padding-top: 70px;
  }

  @include mobile {
    padding-bottom: 340px;
  }
}

.card-client {
  display: flex;
  justify-content: space-between;
  gap: 50px;
  width: 100%;

  &__item {
    flex: 1 1 33.333%;
  }

  &__content {
    position: relative;
    padding: 25px 20px;
    height: 100%;
    background-color: #fff;
    box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.04);
  }

  &__top {
    margin-bottom: 90px;

    @include mobile {
      margin-bottom: 40px;
    }
  }

  &__info {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }

  &__image {
    margin-right: 23px;
    border-radius: 100%;
    width: 76px;
    height: 76px;
    overflow: hidden;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__name {
    max-width: 178px;
    font-family: 'Yeseva One';
    font-weight: 400;
    font-size: 18px;
    line-height: 23px;
    color: #181818;
  }

  &__text {
    margin: 0;
    font-size: 13px;
    line-height: 180%;
    color: #959595;

  }

  &__bottom {
    position: absolute;
    bottom: -30px;
    left: 0;
    display: flex;
    align-items: center;
    width: auto;
    background-color: #E3B873;
    box-shadow: 5px 5px 20px rgba(227, 184, 115, 0.5);
  }

  &__value {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 12px 18px;
    width: 100%;
    border-right: 1px solid #fff;

    img {
      display: block;
      width: 33px;
      height: 33px;
      margin-right: 15px;
    }

    span {
      flex-shrink: 0;
      font-family: 'Yeseva One';
      font-weight: 400;
      font-size: 17px;
      text-transform: uppercase;
      color: #fff;

      @include mobile {
        font-size: 15px;
      }
    }
  }

  &__value:nth-child(2) {
    border-right: none;
  }

  @include desktop {
    gap: 40px;
  }

  @include tablet {
    flex-direction: column;
    gap: 80px;
  }
}
